.NavBar li a:hover, .NavBar li a:focus {
  border-color: #991919;
  outline: 0;
}
.NavBar .fRight{
  float:right;  
}
.NavBar .fLeft{
  float:left;
}
.NavBar li a{
  font-family: "Roboto",sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 400;
  letter-spacing: 2px;
  color: #fff;
  border-bottom: 1px solid;
  border-color: #fff;
  margin: 10px 15px;
  padding: 0 0 2px 0;
}
.NavBar .nav-item{
  text-decoration: none !important;
}
.NavBar .nav {
  background-color: var(--off-white);
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  width: 100%;
  height: 80px;
  z-index: 1000;
  box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15);
}
.NavBar a{
  color: rgb(218, 214, 214);
}
.NavBar a:hover{
  color: #fec503;
  text-decoration: underline;
}
.NavBar .navbar-dark .navbar-nav .nav-link{
  color: rgb(218, 214, 214);
}
.NavBar .navbar-dark .navbar-nav .nav-link:hover{
  color: #fec503 !important;
  text-decoration: underline !important;
}
.navbar-dark .navbar-nav .nav-link:hover, .navbar-dark .navbar-nav .nav-link:focus{
  color: #fec503 !important;
  text-decoration: underline;
}
.NavBar .nav-logo {
  height: 60px;
  width: 60px;
}
.navbar-toggler{
  background-color:#ecb90f;
}
.NavBar .nav-logo:hover {
  cursor: pointer;
}
.NavBar li a:hover{
  text-decoration: none;
  color: #fec503 !important;
}
.NavBar .nav .nav-content {
  max-width: 100%;
  padding: 0rem 3rem;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
.NavBar .nav-item {
  display: inline;
  color: #333;
}
.NavBar .nav-item:hover {
  color: black;
  cursor: pointer;
}
.NavBar .nav-items{
  margin-bottom: 0;
}
.capitalizeLi{
  text-transform: uppercase;
}
.Footer a i:hover{
  color: #fec503 !important;
}
.Footer .center-margin{
  margin: 0 auto;
  text-align: center;
}
.NavBarHome{
 
  margin:0 auto;
}
.Footer{
  
  margin: 0 auto;
}
@media (max-width: 767px){
  .col.icon-col{
    padding-left: 5px;
    padding-right: 5px;
  }
  .Footer .section-content{
    padding-left: 10px !important;
    padding-right: 10px !important;
    white-space: nowrap !important;
  }
  .Footer footer{
    padding-left: 0 !important;
    padding-right: 0 !important;

  }
}
@media (max-width: 986px) {
  .sds_hide-at-lg-nav{
    display: block !important;
  }
  .sds_show-at-lg-nav{
    display: none !important;
  }
  .navigation-items{
      display:none;
  }
}
@media (min-width: 987px) {
  .sds_show-at-lg-nav{
    display: block !important;
  }
  .sds_hide-at-lg-nav{
    display: none !important;
  }
}

@media (min-width: 1200px){
  .container, .container-sm, .container-md, .container-lg, .container-xl {
      max-width: 1920px;
  }
}


html{
    scroll-behavior: smooth;
}
:root {
  --off-white: #fafafa;
}
.section-dark {
  background: #343a40;
  color: white;
}

.section-content {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px;
}
.fullWidth{
  width:100%;
}
h3{
  font-weight: normal !important;
}
i.fab{
  color:#fff;
}
.paddLeft{
    padding-left: 1em;
}
.paddLeft2em{
    padding-left: 2em;
}
.fullWidthCenter{
  max-width:1280px;
  margin: 0 auto;
}

.custom-row-width ul{
  padding-left: .5em !important;
}
.custom-row-width-col{
  padding-left:0 !important;
  padding-right:0 !important;
}
@media (max-width: 767px) {
  .sds_hide-at-lg{
    display: none;
  }
  .mz-about-container{
    padding: 0 !important;
  }
  .row{margin:0 auto;}
  .row-0-gutter{margin:0 auto;}
}

@media (min-width: 768px) {
  .sds_show-at-lg{
    display: block;
  }
}
/* Work Experience Section CSS */
section.work-experience-container li{
    text-align: left;
}
.work-experience-container .pad2emTop{
    padding-top: 2em;
}
.work-experience-container  .pad2emBot{
    padding-bottom: 2em;
}
.work-experience-container  .pad2emTopBot{
    padding: 2em 0;
}

/* overrides */
.modal-dialog{
    max-width: 580px !important;
}
.modal-content{
    padding: 1em !important;
}
.modal-works span{
    color: #000;
}
figure.effect-bubba h2{
    font-size: 1.5em !important;
}
.modal-works{
    text-align: center;
    margin: 0 auto;
}
figure.effect-bubba:hover img{
    opacity: 0 !important;
}
figure.effect-bubba img{
    opacity: 0.3 !important;
}
figure.effect-bubba:hover h2{
    color:#fed136;
}
figure.effect-bubba p{
    padding: 20px 3.5em !important;
}
/* custom */
.mar04em{
    margin-bottom:.4em;
}
.modalLang{
    margin-bottom: .3em;
}
.dateLocationModal{
    text-align: center;
    font-weight: bold;
}
.modalTitle{
    font-weight: bold;
}
